import Axios from "axios";
import * as actionTypes from "./actionTypes";

export const authStart = () => {
	return {
		type: actionTypes.AUTH_START,
	};
};

export const authSuccess = (token, userId) => {
	return {
		type: actionTypes.AUTH_SUCCESS,
		idToken: token,
		userId: userId,
	};
};

export const authFail = (error) => {
	return {
		type: actionTypes.AUTH_FAIL,
		error: error,
	};
};

export const logOut = () => {
	localStorage.removeItem("token");
	localStorage.removeItem("expireDate");
	localStorage.removeItem("userId");
	return {
		type: actionTypes.AUTH_LOGOUT,
	};
};

export const checkAuthTimeOut = (expirationTime) => {
	return (dispatch) => {
		setTimeout(() => {
			dispatch(logOut());
		}, expirationTime * 1000);
	};
};

export const setAuthRedirectPath = (path) => {
	return {
		type: actionTypes.SET_AUTH_REDIRECT_PATH,
		path: path,
	};
};

export const auth = (email, password, isSignUp) => {
	return (dispatch) => {
		dispatch(authStart());
		const authData = {
			email: email,
			password: password,
			returnSecureToken: true,
		};
		let url =
			"https://identitytoolkit.googleapis.com/v1/accounts:signUp?key=AIzaSyBf9nvmsf_6gf8x-VZhqTAmSz4O6dM7EXo";
		if (!isSignUp) {
			url =
				"https://identitytoolkit.googleapis.com/v1/accounts:signInWithPassword?key=AIzaSyBf9nvmsf_6gf8x-VZhqTAmSz4O6dM7EXo";
		}
		Axios.post(url, authData)
			.then((response) => {
				const expirationDate = new Date(
					new Date().getTime() +
						response.data.expiresIn * 1000,
				);
				localStorage.setItem("token", response.data.idToken);
				localStorage.setItem("expireDate", expirationDate);
				localStorage.setItem("userId", response.data.localId);
				dispatch(
					authSuccess(
						response.data.idToken,
						response.data.localId,
					),
				);
				dispatch(checkAuthTimeOut(response.data.expiresIn));
			})
			.catch((error) => {
				dispatch(authFail(error.response.data.error));
			});
	};
};

export const authCheckState = () => {
	return (dispatch) => {
		const token = localStorage.getItem("token");

		if (!token) {
			dispatch(logOut());
		} else {
			const expireDate = new Date(
				localStorage.getItem("expireDate"),
			);
			if (expireDate <= new Date()) {
				dispatch(logOut());
			} else {
				const userId = localStorage.getItem("userId");
				dispatch(authSuccess(token, userId));
				dispatch(
					checkAuthTimeOut(
						(expireDate.getTime() -
							new Date().getTime()) /
							1000,
					),
				);
			}
		}
	};
};



